<script>

export default {
  props: {
    invoice: {
      type: Object,
      required: true,
    },
  },

  data () {
    return {
      fields: [
        {
          name: 'date',
          title: 'Date',
          titleClass: 'ps-3',
          dataClass: 'ps-3',
          callback: this.$options.filters.date_DATE_MONTH_YEAR,
          width: '10%',
        },
        {
          name: 'description',
          title: 'Description',
          titleClass: 'ps-3',
          dataClass: 'ps-3',
          width: '20%',
        },
        {
          name: 'department.name',
          title: 'Department',
          titleClass: 'ps-3',
          dataClass: 'ps-3',
          width: '20%',
        },
      ],

      moreParams: {
        filter: {
          ledger_id: this.invoice.id,
          // has_procedure_items: true,
          done: true,
        },
      },

      resultPdf: '',

      invoices: [],
    }
  },

  computed: {
    vuetable () {
      return this.$refs.table.$refs.vuetable
    },

    url () {
      return '/results/' + this.invoice.hashid + '/grouped/pdf'
    },
  },

  watch: {
    'moreParams.filter.header_id': function (newId) {
      if (newId) {
        this.vuetable.reload()
      }
    },
  },

  methods: {
    showResults (results) {
      this.$httpClient.get('/results', {
        params: {
          header_id: this.invoice.id,
        },
        responseType: 'blob',
      })
        .then(({ data }) => {
          this.resultPdf = URL.createObjectURL(
            new Blob([data], { type: 'application/pdf' }),
          )
        }).catch(e => {
          console.error(e)
        })
    },

    downloadResult (rowData) {
      this.$httpClient.get(`/results/${rowData.ledger_id}/${rowData.id}/pdf`, {
        responseType: 'blob',
      })
        .then(({ data }) => {
          this.resultPdf = URL.createObjectURL(
            new Blob([data], { type: 'application/pdf' }),
          )
        }).catch()
    },

    printResults () {
      window.open('/results/' + this.invoice.hashid + '/grouped/pdf')
    },
  },
}
</script>

